.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  color: white;
  text-align: center;
}

.About {

}

.About-Title {
  font-size: 3em;
}

.About-Summary {
  text-align: center;
  font-size: 2em;
}

.Team {

}

.Team-Title {
  font-size: 3em;
}

.Team-MemberCard {
  display: flex;
  flex-direction: column;
}

.Team-MemberName {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: -1%;
}

.Team-MemberRole {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberImage {
  width: 150px;
  height: 200px;
  object-fit: contain;
  border-radius: 10%;
  margin-bottom: -1%;
  margin-top: 3%;
  margin-right: auto;
  margin-left: auto;
}

.Team-MemberBio {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberLinkedin {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberOther {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberEmail {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberPhone {
  font-size: 1em;
  margin-bottom: -1%;
}

.Tutorial {

}

.Tutorial-Title {
  font-size: 3em;
}
